import { CHANGE_CURRENT_PAGE,ONLOAD_DATA, LOAD_WORLD } from './types';

const INITIAL_STATE = {
  currentModule: 'Home',
  data:null,
  countries: ['India','France','China','Canada'],
  currency: ['USD', 'CAD','GDP','INR','EUR']
};

export default function (state = INITIAL_STATE, action) {
  switch (action.type) {
    case CHANGE_CURRENT_PAGE:
      return {
        ...state,
        currentModule: action.payload
      };
    case ONLOAD_DATA.success:
      return{
        ...state,
        data:action.payload
      }
    case LOAD_WORLD.success:
      // const countries = action.payload.map(({name}) => name);
      return{
        ...state
      }
    default:
      return state;
  }
}
