import React, { useState, useEffect } from 'react';
import Loading from '../../components/loading';
import { useDispatch, useSelector } from 'react-redux';
import Header from '../Header/Header';
import Main1 from './Main1/Main1';
import Main2 from './Main2/Main2';
import Main3 from './Main3/Main3';
import Footer from '../Footer/Footer';
import MailingList from './Containers/MailingList';
import AboutUS from '../AboutUs/aboutUs';
import ContactUs from './Containers/ContactUs';
import { onLoad } from '../../general_redux/actions';

function Home() {
  const [viewMailing, setViewMailing] = useState(false);
  const [viewContactUs, setViewContactUs] = useState(false);
  const [viewAbout, setViewAbout] = useState(false);
  const dispatch = useDispatch();
  const mailListView = () => {
    setViewAbout(false);
    setViewMailing(true);
  };
  useEffect(() => {
    dispatch(onLoad());
  }, []);

  const actionForAddSubscriber = useSelector(({ subscribers }) => subscribers && subscribers.actionForAddSubscriber);

  return (
    <>
      <Loading  loadingStatus={actionForAddSubscriber.loading}/>
      <MailingList
        show={viewMailing}
        onHide={() => {
          setViewMailing(false);
        }}
      />
      <ContactUs
        show={viewContactUs}
        onHide={() => {
          setViewContactUs(false);
        }}
      />
      <AboutUS
        show={viewAbout}
        onHide={() => {
          setViewAbout(false);
        }}
        setMailModal={mailListView}
        setViewContactUs={() => {
          setViewAbout(false);
          setViewContactUs(true);
        }}
      />
      <Header aboutModal={() => setViewAbout(true)} />
      <Main1 setMailModal={() => setViewMailing(true)} />
      <Main2 setMailModal={() => setViewMailing(true)} />
      <Main3 setViewContactUs={() => setViewContactUs(true)} setMailModal={() => setViewMailing(true)} />
      <Footer setViewContactUs={() => setViewContactUs(true)} />
    </>
  );
}
export default Home;
