import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Container, Modal,Row,Col } from 'react-bootstrap';
import moment from 'moment';
import { getBookingByGuestId } from '../../../../../../general_redux/calendar/actions';
import Loading from '../../../../../../components/loading';
import { deleteBooking } from '../../../../../../general_redux/calendar/actions';
import EditBooking from '../../../Calendar/components/NewBooking/AddNewBooking';
import Table from '../../../../../../components/Table';

function GuestBookingList({id}) {
  const bookingList = useSelector(({ calendar }) => calendar && calendar.bookingListByGuest);
  const properties = useSelector(({ rentals }) => rentals && rentals.rentals);
  const actionForGetBookingListById = useSelector(({ calendar }) => calendar && calendar.actionForGetBookingListById);
  const dateFormatArray= useSelector(({generalSetting})=>generalSetting && generalSetting.dateFormatArray);
  const dateFormat = useSelector(({generalSetting})=>generalSetting && generalSetting.setting.dateDisplay);
  const dateFinal= dateFormatArray.filter((format)=>format.id===dateFormat)[0];

  const [ displayList, setDisplayList ] = useState([]);
  const [ editBooking, setEditBooking] = useState(false);
  const [ editBookingData, setEditBookingData ] = useState(null);
  const dispatch = useDispatch();

  const cols = [
    { lable: 'check In', key: 'arrive' ,isDate:true },
    { lable: 'check Out', key: 'depart', isDate:true },
    { lable: 'Nights', key: 'nights' },
    { lable: 'Rental', key: 'rental' },
    { lable: 'Channel', key: 'source' },
    { lable: 'Total', key: 'price' },
    { lable: 'Status', key: 'status' },
    { lable: 'Actions', key: '' },
  ];

  useEffect ( ()=>{
    dispatch(getBookingByGuestId(id));
  },[]);

  useEffect ( ()=>{
    setDisplayList([]);
    bookingList.map( booking => {
      const data = {
        ...booking,
        rental: properties.filter( property => property.id === booking.rentalId)[0].name,
       }
      booking.status !== "Cancelled" && setDisplayList(displayList => [...displayList, data]);
    })

  },[bookingList]);

  function handleDelete({id}){
    dispatch(deleteBooking(id));
  }

  function handleEdit({id}){
    setEditBookingData(bookingList.filter( booking => booking.id === id)[0]);
    setEditBooking(true);
  }

  return (
      <div>
        <Modal
            show={editBooking}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
            onHide={() => {
              setEditBooking(false);
            }}
        >
          <EditBooking
              viewForEdit={editBookingData}
              isEdit={true}
          />
        </Modal>
        <Loading  loadingStatus={actionForGetBookingListById.loading}/>
        {/*<AppTable */}
        {/*  cols={cols} */}
        {/*  tableName="Booking"*/}
        {/*  rows={displayList} */}
        {/*  isView */}
        {/*  isOnlyEdit */}
        {/*  isDelete */}
        {/*  onDelete={handleDelete} */}
        {/*  //onView={handleEdit}*/}
        {/*  dateFormat={dateFormat} />*/}
        <Container fluid>
        <Row className="justify-content-center">
          <Col xs={12} md={10} className={`pt-3`}>
            <Table
                rows={displayList}
                cols={cols}
                dateFormat={dateFinal.val}
                startKey={cols[0].key}
                onEdit={()=>console.log("under construction")}
            />
            </Col>
          </Row>
        </Container>
      </div>
  );
}
export default GuestBookingList;
