import React from 'react';
import {Form, InputGroup } from 'react-bootstrap';
import ModalButton from '../../../../../../components/ModalButton';
import { Formik } from 'formik';

function GroupForm(props) {
  const { schema, handleSubmit, options } = props;
  return (
    <>
      <Formik
        validationSchema={schema}
        initialValues={
          props.initValue
            ? props.initValue
            : {
                groupName: '',
                color: '',
              }
        }
        onSubmit={handleSubmit}
        validateOnChange={false}
        validateOnBlur={false}
      >
        {({ errors, handleChange, handleSubmit, touched, initialValues }) => (
          <Form onSubmit={handleSubmit}>
            <Form.Row>
              <Form.Label className="col-sm-4 col-form-label">Group name:</Form.Label>
              <div className="col-md-6">
                <Form.Control
                  type="text"
                  name="groupName"
                  onChange={handleChange}
                  isInvalid={!!errors.groupName}
                  defaultValue={initialValues.groupName}
                  style={{fontSize:'inherit'}}
                />
                <Form.Control.Feedback type="invalid">{errors.groupName}</Form.Control.Feedback>
              </div>
            </Form.Row>
            <br />
            <Form.Row>
              <Form.Label className="col-sm-4 col-form-label">Color:</Form.Label>
              <div className="col-md-6">
                <Form.Control as="select" name="color" style={{fontSize:'inherit'}} onChange={handleChange} isInvalid={!!errors.color}>
                  <option key={'selectC'} selected={true} disabled={true}>
                    ----Select----
                  </option>
                  {options.map((opt) => (
                    <option key={opt} selected={initialValues.color === opt}>
                      {opt}
                    </option>
                  ))}
                </Form.Control>
                <Form.Control.Feedback type="invalid">{errors.color}</Form.Control.Feedback>
              </div>
            </Form.Row>
            <br />
              <ModalButton btnText={"Save changes"}/>
          </Form>
        )}
      </Formik>
    </>
  );
}

export default GroupForm;
