import React, { useState, useEffect } from 'react';
import { Link, useHistory } from 'react-router-dom';
import {Spinner} from 'react-bootstrap';
import styles from './login.module.css';
import Header from '../Header/Header';
import Footer from '../Footer/Footer';
import logo from '../../icons/logo.png';
import {useSelector,useDispatch} from 'react-redux';
import Loading from '../../components/loading';
import { oauthLoginUser, loginUser } from '../../general_redux/user/actions';
import ContactUs from '../Home/Containers/ContactUs';

function Login() {
  const [email, setEmail] = useState('');
  const [viewContactUs, setViewContactUs] = useState(false);
  const loginStatus = useSelector(({ user }) => user && user.loginAction);
  const dispatch = useDispatch();

  const login = (e) => {
    e.preventDefault();
    dispatch(loginUser(email))
  }

  const oauthLogin = (e, source) => {
    e.preventDefault();
    dispatch(oauthLoginUser(e.target.getAttribute("source")));
  }

  return (
      <div>
        
        <Header redirect/>
        <ContactUs
            show={viewContactUs}
            onHide={() => {
              setViewContactUs(false);
            }}
        />
        <div className="d-flex justify-content-center">
          <div className={`${styles.mainContainer} ${styles.login}`}>
            <div className={` ${styles.login_wrapper} shadow`}>
              <div className="d-flex justify-content-center">
                <Link to="/">
                  <img src={logo} className={styles.login_image} />
                </Link>
              </div>
              <div
                  className={`d-flex justify-content-center ${styles.login_header}`}
              >
                <h5>User Login</h5>
              </div>
              <div className="d-flex justify-content-center">
                <form onSubmit={login}>
                  <div className={`input-group mb-3 ${styles.input_group}`}>
                    <div className="input-group-prepend">
                    <span
                        className={`input-group-text ${styles.input_group_text}`}
                        id="emailAddress"
                    >
                      Email address:
                    </span>
                    </div>
                    <input
                        onChange={(e) => setEmail(e.target.value)}
                        type="email"
                        value={email}
                        className={`form-control ${styles.form_control}`}
                        aria-label="emailAddress"
                        aria-describedby="emailAddress"
                        required
                    />
                  </div>
                  <div className={styles.center}>
                    <button className={styles.login_submit} type="submit" disabled={loginStatus.loading}>{
                      (loginStatus.loading)?(
                        <>
                        <Spinner
                          as="span"
                          animation="border"
                          role="status"
                          aria-hidden="true"
                          style={{marginRight:'1em',height:'1.3em',width:'1.3em'}}
                        /><span>Login</span>
                        </>
                      ):(
                        <span>Login</span>
                      )
                    }
                    </button>
                  </div>
                </form>
              </div>
              <div className={styles.login_separator}>
                ------------------------- OR -------------------------
              </div>
              <div className="justify-content-center">
                <div className={styles.login_socialmedia}>
                  <form onSubmit={oauthLogin} source="google">
                    <button
                        className={` ${styles.login_submit} ${styles.login_google}`}
                    >
                      <i className={`fa fa-google ${styles.fa_google}`}/> |
                      Sign in with Google
                    </button>
                  </form>
                </div>
                ​
                <div className={styles.login_socialmedia}>
                  <form onSubmit={oauthLogin} source="facebook">
                    <button
                        className={` ${styles.login_submit} ${styles.login_facebook}`}
                    >
                      <i
                          className={`fa fa-facebook-f ${styles.fa_facebook}`}
                      />{' '}
                      | Sign in with Facebook
                    </button>
                  </form>
                  <Link to="/register" className={styles.login_create}>
                    Create an Account
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer setViewContactUs={() => setViewContactUs(true)} />
      </div>
  );
}

export default Login;