import React from 'react';
import { Modal } from 'react-bootstrap';
import RateForm from './RateForm'
import * as yup from 'yup';

function AddRate({show, onHide}) {
  const schemaRate = yup.object({
    dateFrom: yup.date().required(),
    dateTo: yup.date().required(),
    rate: yup.number().required().min(0),
    minStay: yup.number().required().min(1),
  });

  const updatedSubmit = ( data )=>{
    console.log('PAYLOAD: ', data)
    onHide();
  }

  return (
    <Modal 
      show = {show}
      onHide = {onHide}
      size="md"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">Set rate</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <RateForm
          schema = {schemaRate}
          updatedSubmit = {updatedSubmit}
        />
      </Modal.Body>
    </Modal>
  );
}

export default AddRate;
