import React, { useState, useEffect } from 'react';
import { Modal } from 'react-bootstrap';
import { useSelector, useDispatch } from 'react-redux';

import styles from './editBooking.module.css';
import BookingDetails from './components/BookingDetails';
import GuestDetails from './components/GuestDetails';
import Payment from './components/Payment';
import Invoice from './components/Invoice';
import { getGuestByBooking } from '../../../../../../general_redux/guest/actions';

const EditBooking = ({ bookingData,handleEditBooking }) => {
  const dispatch = useDispatch();
  const [currentTab, setCurrentTab] = useState('Booking Details');
  const tabs = ['Booking Details', 'Guest Details', 'Payment', 'Invoice'];

  const guestList = useSelector(({ guests }) => guests && guests.guestList);
  const guestByBooking = useSelector(({ guests }) => guests && guests.guestByBooking)[0];
  const guestDetails = guestList.filter((guest) => guest.id === guestByBooking.id)[0];
  const rentals = useSelector(({ rentals }) => rentals && rentals.rentals);

  useEffect(() => {
    dispatch(getGuestByBooking(bookingData.id));
  }, []);

  return (
    <>
      <Modal.Header closeButton>
        <Modal.Title>Edit Booking</Modal.Title>
      </Modal.Header>
      <Modal.Body className={`px-sm-2 px-0 ${styles.modal_body}`}>
        <div className={styles.upperRow}>
          {tabs.map((tab, index) => (
            <a
              className={currentTab === tab ? `${styles.tabBox} ${styles.active}` : `${styles.tabBox}`}
              key={index}
              onClick={() => setCurrentTab(tab)}
            >
              {tab}
            </a>
          ))}
        </div>

        <div>
          {currentTab === 'Booking Details' && (
            <BookingDetails
                rentals={rentals}
                bookingData={bookingData}
                guestDetails={guestDetails}
            />
          )}
          {currentTab === 'Guest Details' && <GuestDetails guestDetails={guestDetails} />}
          {currentTab === 'Payment' && <Payment />}
          {currentTab === 'Invoice' && <Invoice />}
        </div>
      </Modal.Body>
    </>
  );
};

export default EditBooking;
